	module.exports = {"data":{"post":{"html":"<p>Keep in mind that the three big news stories of this weekend were the capture of Khalid Shaikh Mohammed, the rejection by the Turkish parliament of the deal to allow the US to attack Iraq from Turkey, and the destruction of the first Samoud missiles by Iraq. Here then were the top headlines in this morning's local paper <a title=\"Hamburger Abendblatt Online\" href=\"http://www.abendblatt.de/\">Hamburger Abendblatt</a>:</p>\n<p>* Poll disaster for SPD in the North (local elections in Schleswig-Holstein)</p>\n<p>* Fall of an Idol (footballer Oliver Kahn admits having affair while wife is pregnant)</p>\n<p>* Ja Ja Juliette (semi-finals of 'Deutschland sucht den Superstar', a form of 'American Idol')</p>\n<p>For comic relief, a report on private detectives seeking out workers who fake sick was also mentioned on the front page... on Rosenmontag, the highlight of Karneval (which is not celebrated in Hamburg).</p>","fields":{"title":"Today's Headlines","date":"03 Mar 2003","url":"https://papascott.de/2003-03-03-todays-headlines/"}}},"pathContext":{"slug":"/2003-03-03-todays-headlines/","prev":{"fields":{"title":"Time Shift","date":"28 Feb 2003","slug":"/2003-02-28-time-shift/"}},"next":{"fields":{"title":"Leave the Church in the Village","date":"04 Mar 2003","slug":"/2003-03-04-leave-the-church-in-the-village/"}}}}

